import styles from './index.module.less';
import {Component} from 'san';
import template from './index.html';
import TitleOnly from '../titleOnly';
import TextImage from '../textImage';
import {invoke} from "@baidu/xbox";
import AucComponent from "@baidu/auc-base/AucComponent";
import {ACT_TYPE, sendLog} from "@/pages/hotComment/utils/log";
import {env} from "@baidu/auc-base/env";

export default class ListItem extends AucComponent {
    static template = template;

    initData() {
        return {
            styles,
            active: false,
        }
    }

    static components = {
        'title-only': TitleOnly,
        'text-image': TextImage
    }

    attached() {
        this.registerScrollInView().then(() => {
            sendLog(ACT_TYPE.SHOW, this.data.get('data.id'));
        });
    }

    clickItem() {
        sendLog(ACT_TYPE.CLICK, this.data.get('data.id'));
        if (env.isBoxSeries) {
            invoke(this.data.get('data.cmd'));
        } else {
            location.href = this.data.get('data.url');
        }
    }

    clickComment() {
        sendLog(ACT_TYPE.CLICK, this.data.get('data.id'));
        if (env.isBoxSeries) {
            invoke(this.data.get('data.commentInfo.cmd'));
        } else {
            location.href = this.data.get('data.url');
        }
    }

    touchStart() {
        this.data.set('active', true);
    }

    touchEnd() {
        this.data.set('active', false);
    }
}
