import {builder} from 'san-update';
import {fuzzyReplace} from '@baidu/xbox-emoticon';

enum ComponentName {
    TitleOnly = 'title-only',
    TextImage = 'text-image',
}

// 业务字段映射
const layoutMap = {
    image1: ComponentName.TextImage,
    titleonly: ComponentName.TitleOnly,
    image3: ComponentName.TextImage
}

interface CommentInfo {
    cmd: string;
    hotTag: boolean;
    content: string;
    commentNum: number;
}

interface BaseData {
    title: string;
    id: string;
    url: string;
    authorName: string;
    commentInfo: CommentInfo;
    cmd: string;
    rank: number;
    componentName: string;
    tips: string[];
    type: 'text' | 'video';
}

interface ImageOneData extends BaseData {
    image: string;
    duration?: string;
}

type DataInfo = ImageData | BaseData;

enum SOURCE_TYPE {
    VIDEO = 'video', // 视频
    TEXT = 'text' // 图文
}

const getSourceUrl = (type: SOURCE_TYPE, id: string) => {
    switch (type) {
        case SOURCE_TYPE.VIDEO:
            return `https://mbd.baidu.com/newspage/data/videoshare?nid=${id}&source=hotComment`;
        case SOURCE_TYPE.TEXT:
            return `https://mbd.baidu.com/newspage/data/landingshare?context=${encodeURIComponent(JSON.stringify({nid: id}))}&page=landingshare`
        default:
            return '';
    }
};

const resolveBaseData = (item): BaseData => {
    const tips = [];
    if (item.data.source) {
        tips.push(item.data.source);
    }
    if (item.data.comment_num) {
        tips.push(`${item.data.comment_num}人评论`);
    }
    const type = item.data.mode === 'video' ? SOURCE_TYPE.VIDEO : SOURCE_TYPE.TEXT;

    return {
        title: item.data.title,
        authorName: item.data.source,
        rank: +item.rank_num,
        id: item.id,
        cmd: item.data?.cmd?.url,
        url: getSourceUrl(type, item.id),
        componentName: layoutMap[item.layout],
        type,
        tips,
        commentInfo: {
            cmd: item.data.comment_bar?.scheme || '',
            hotTag: !!item.data.comment_bar?.god_comment,
            content: fuzzyReplace(item.data.comment_bar?.content || '', 'hotComment'),
            commentNum: +item.data.comment_num
        }
    }
};

const resolveImageOneData = (item): ImageOneData => {
    return {
        ...resolveBaseData(item),
        image: item.data.image,
        duration: item.data.duration
    }
}

const resolveImageThreeData = (item): ImageOneData => {
    return {
        ...resolveBaseData(item),
        image: item.data.items[0],
        duration: item.data.duration
    }
}

const resolveList = (list): DataInfo[] => {
    const resolveMap = {
        'titleonly': resolveBaseData,
        'image1': resolveImageOneData,
        'image3': resolveImageThreeData
    }

    return list.map((item, index) => {
        return resolveMap[item.layout](item, index)
    });
}

export default {
    'main:updateItemList'(itemList, {dispatch, getState}) {
        const preList = getState('main.itemList');
        let resolvedList = resolveList(itemList);
        let newList = preList.concat(resolvedList);
        return builder().set(`main.itemList`, newList);
    },
    'main:updateHeadImage'(imageObj) {
        return builder().set(`main.headImage`, imageObj);
    }
};
