import './index.less';
import {Component} from 'san';
import template from './index.html';

export default class Comment extends Component {
    static template = template;

    static components = {
    }

    initData() {
        return {
            active: false
        }
    }

    async attached() {
    }

    touchStart(e) {
        this.data.set('active', true);
        e.stopPropagation();
    }

    touchEnd(e) {
        this.data.set('active', false);
        e.stopPropagation();
    }

    clickComment(e) {
        this.fire('click-comment', '');
        e.stopPropagation();
    }
}
