import {Component} from 'san';

class Tags extends Component {
    static template = /* html */ `
        <div class="tagWrap">
            <div
                s-for="tag in tags"
                class="{{tagClass}}"
                style="{{tagStyle}}"
            >{{tag}}</div>
        </div>
    `;
}

export default Tags;
