import {Component} from 'san';
import WujiLabel from '../../wuji-label';

const prefixCls = 'coupon';

export default class Coupon extends Component {
    static template = /* html */ `
        <div class="${prefixCls}">
            <wuji-label
                class="${prefixCls}-label"
                s-for="item in data"
                s-bind="item"
            />
        </div>
    `;

    static components = {
        'wuji-label': WujiLabel,
    };
}
