import './style/index.less';
import {Component, DataTypes} from 'san';

const prefixCls = 'w-poi';

export default class WujiPoi extends Component {
    static template = /* html */ `
        <div class="${prefixCls}">
            <div class="${prefixCls}-name">{{name}}</div>
            <div class="${prefixCls}-distance">{{distance}}</div>
        </div>
    `;

    static dataTypes = {
        name: DataTypes.string,
        distance: DataTypes.string,
    };

    initData() {
        return {
            name: '店铺名',
            distance: '距离',
        };
    }
}
