import './index.less';
import {Component} from 'san';
import template from './index.html';
import {WujiImage} from '@baidu/wuji-san';
import Title from '../title';
import Comment from '../comment';
import '@baidu/wuji-icons/svg/d20-capsule-video-f-30.svg';

export default class TextImage extends Component {
    static template = template;

    static components = {
        'wuji-image': WujiImage,
        'title': Title,
        comment: Comment
    }

    static computed = {
        labelIcon() {
            let type = this.data.get('type');
            return type === 'video' ? 'd20-capsule-video-f-30' : '';
        },
        labelText() {
            let duration = this.data.get('duration');
            let type = this.data.get('type');
            return type === 'video' ? duration : '';
        }
    }

    clickComment() {
        this.fire('click-comment', '');
    }

    initData() {

    }

    async attached() {
    }
}
