tem {\n  margin-top: 0.17rem;\n}\n.w-table-covid-title:first-child,\n.w-table-covid-item:first-child {\n  margin-top: 0;\n}\n.w-table-covid .text {\n  padding-top: 0.06rem;\n  font-size: 0.17rem;\n  font-weight: 600;\n  line-height: 1;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 1;\n  -webkit-box-orient: vertical;\n  word-break: break-all;\n}\n.android .w-table-covid .text {\n  font-weight: unset;\n  -webkit-text-stroke-width: 0.004rem;\n}\n.w-table-covid .text,\n.w-table-covid .text-x123 {\n  color: rgba(71, 109, 160, 1);\n}\n.w-table-covid .text-x118 {\n  color: rgba(232, 49, 50, 1);\n}\n.w-table-covid .text-x119 {\n  color: rgba(236, 146, 23, 1);\n}\n.w-table-covid .text-x120 {\n  color: rgba(16, 174, 181, 1);\n}\n.w-table-covid .text-x121 {\n  color: rgba(77, 80, 84, 1);\n}\n.w-table-covid .text-x122 {\n  color: rgba(255, 106, 87, 1);\n}\n.w-table-covid.table-type-2 .w-table-covid-title,\n.w-table-covid.table-type-2 .w-table-covid-item {\n  margin-top: 0;\n}\n.w-table-covid.table-type-2 .text,\n.w-table-covid.table-type-2 .w-table-covid-title:first-child {\n  padding-top: 0.12rem;\n}\n@media screen and (max-width: 320px) {\n  .w-table-covid .text {\n    font-size: 14px;\n  }\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-san/es/wuji-table-covid/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/spacing/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/font/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-san/es/style/mix