exports.push([module.id, "/**\n * @file 组件样式 无极-左文右图\n * @author baozhixin <baozhixin@baidu.com>\n */\n.w-text-image {\n  position: relative;\n  padding-top: 0.15rem;\n  padding-bottom: 0.15rem;\n}\n.w-text-image-left {\n  display: -webkit-flex;\n  display: flex;\n  -webkit-flex-direction: column;\n          flex-direction: column;\n}\n.w-text-image-right {\n  -webkit-flex-shrink: 0;\n          flex-shrink: 0;\n}\n.w-text-image .w-widget {\n  margin-top: 0.12rem;\n}\n.w-text-image:active {\n  background-color: rgba(0, 0, 0, 0.05);\n}\n.darkmode .w-text-image:active {\n  background-color: rgba(255, 255, 255, 0.1);\n}\n.w-text-image-hairline {\n  position: absolute !important;\n  bottom: 0;\n  left: 0.17rem;\n  right: 0.17rem;\n  width: auto;\n  position: relative;\n  background-color: transparent;\n  height: 1px;\n}\n.w-text-image-hairline:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  -webkit-transform-origin: 0 0;\n          transform-origin: 0 0;\n  height: 1px;\n  width: 100%;\n  background-color: rgba(238, 238, 238, 1);\n}\n.darkmode .w-text-image-hairline:after {\n  background-color: rgba(102, 102, 102, 0.4);\n}\n@media (-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx) {\n  .w-text-image-hairline:after {\n    -webkit-transform: scaleY(0.66666667);\n            transform: scaleY(0.66666667);\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n  .w-text-image-hairline:after {\n    -webkit-transform: scaleY(0.5);\n            transform: scaleY(0.5);\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .w-text-image-hairline:after {\n    -webkit-transform: scaleY(0.33333333);\n            transform: scaleY(0.33333333);\n  }\n}\n.w-text-image-center {\n  -webkit-justify-content: center;\n          justify-content: center;\n}\n.w-text-image-flex-start {\n  -webkit-justify-content: flex-start;\n          justify-content: flex-start;\n}\n.w-text-image-flex-end {\n  -webkit-justify-content: flex-end;\n          justify-content: flex-end;\n}\n.w-text-image-space-between {\n  -webkit-justify-content: space-between;\n          justify-content: space-between;\n}\n.w-text-image-space-around {\n  -webkit-justify-content: space-around;\n          justify-content: space-around;\n}\n.w-text-image-reverse .w-row {\n  -webkit-flex-direction: row-reverse;\n          flex-direction: row-reverse;\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-san/es/wuji-text-image/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/spacing/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/line/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/mixins.less"],"names":[],"mappings":"AAAA;;;EAGE;ACHD;ECaG,kBAAA;ECwFA,oBAAA;EACA,uBAAA;AH9FJ;AEQI;EACI,qBAAA;EAAA,aAAA;EACA,8BAAA;UAAA,sBAAA;AFNR;AESI;EACI,sBAAA;UAAA,cAAA;AFPR;ACfC;EEcG,mBAAA;AHIJ;AEWI;EEdA,qCAAA;AJMJ;ACrBC;EGsBe,0CAAA;AJEhB;AESI;EACI,6BAAA;EACA,SAAA;ECuFJ,aAAA;EAVA,cAAA;EEiEA,WAAA;EAYI,kBAAA;EACA,6BAAA;EACA,WAAA;AL7JR;AKkKQ;EACI,WAAA;EACA,kBAAA;EACA,OAAA;EACA,MAAA;EACA,6BAAA;UAAA,qBAAA;EACA,WAAA;EACA,WAAA;ED5LR,wCAAA;AJ6BJ;AC5CC;EGsBe,0CAAA;AJyBhB;AMnCQ;EDoMI;IACI,qCAAA;YAAA,6BAAA;EL9Jd;AACF;AMlCQ;EDoMI;IACI,8BAAA;YAAA,sBAAA;EL/Jd;AACF;AMjCQ;EDoMI;IACI,qCAAA;YAAA,6BAAA;ELhKd;AACF;AEpBI;EACI,+BAAA;UAAA,uBAAA;AFsBR;AEnBI;EACI,mCAAA;UAAA,2BAAA;AFqBR;AElBI;EACI,iCAAA;UAAA,yBAAA;AFoBR;AEjBI;EACI,sCAAA;UAAA,8BAAA;AFmBR;AEhBI;EACI,qCAAA;UAAA,6BAAA;AFkBR;AC7EC;ECgEO,mCAAA;UAAA,2BAAA;AFgBR","file":"index.less","sourcesContent":["/**\n * @file 组件样式 无极-左文右图\n * @author baozhixin <baozhixin@baidu.com>\n */\n.w-text-image {\n  position: relative;\n  padding-top: 45pr;\n  padding-bottom: 45pr;\n}\n.w-text-image-left {\n  display: flex;\n  flex-direction: column;\n}\n.w-text-image-right {\n  flex-shrink: 0;\n}\n.w-text-image .w-widget {\n  margin-top: 36pr;\n}\n.w-text-image:active {\n  background-color: rgba(0, 0, 0, 0.05);\n}\n.darkmode .w-text-image:active {\n  background-color: rgba(255, 255, 255, 0.1);\n}\n.w-text-image-hairline {\n  position: absolute !important;\n  bottom: 0;\n  left: 51pr;\n  right: 51pr;\n  width: auto;\n  position: relative;\n  background-color: transparent;\n  height: 1px;\n}\n.w-text-image-hairline:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  transform-origin: 0 0;\n  height: 1px;\n  width: 100%;\n  background-color: rgba(238, 238, 238, 1);\n}\n.darkmode .w-text-image-hairline:after {\n  background-color: rgba(102, 102, 102, 0.4);\n}\n@media (-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx) {\n  .w-text-image-hairline:after {\n    transform: scaleY(0.66666667);\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n  .w-text-image-hairline:after {\n    transform: scaleY(0.5);\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .w-text-image-hairline:after {\n    transform: scaleY(0.33333333);\n  }\n}\n.w-text-image-center {\n  justify-content: center;\n}\n.w-text-image-flex-start {\n  justify-content: flex-start;\n}\n.w-text-image-flex-end {\n  justify-content: flex-end;\n}\n.w-text-image-space-between {\n  justify-content: space-between;\n}\n.w-text-image-space-around {\n  justify-content: space-around;\n}\n.w-text-image-reverse .w-row {\n  flex-direction: row-reverse;\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 无极-左文右图\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import (reference) '../../style/index.less';\n\n@prefix-cls: w-text-image;\n@title-cls: w-title;\n@widget-cls: w-widget;\n@row-cls: w-row;\n\n.@{prefix-cls} {\n    position: relative;\n    .wuji-py(M_H_X005);\n\n    &-left {\n        display: flex;\n        flex-direction: column;\n    }\n\n    &-right {\n        flex-shrink: 0;\n    }\n\n    .@{widget-cls} {\n        .wuji-mt(M_H_X004);\n    }\n\n    &:active {\n        .wuji-background(CAM_X361);\n    }\n\n    &-hairline {\n        position: absolute !important;\n        bottom: 0;\n        .wuji-left(M_W_X010);\n        .wuji-right(M_W_X010);\n        .wuji-divider(L_X01, CAM_X417);\n    }\n\n    // 左侧文字区域justify布局\n    &-center {\n        justify-content: center;\n    }\n\n    &-flex-start {\n        justify-content: flex-start;\n    }\n\n    &-flex-end {\n        justify-content: flex-end;\n    }\n\n    &-space-between {\n        justify-content: space-between;\n    }\n\n    &-space-around {\n        justify-content: space-around;\n    }\n\n    // 反转图文位置\n    &-reverse .@{row-cls} {\n        flex-direction: row-reverse;\n    }\n}\n","/**\n * @file spacing mixins\n * @author baozhixin <baozhixin@baidu.com>\n *\n * wuji-mx/wuji-my/wuji-px/wuji-py 没有先赋值一个变量的原因参考 plugin-less 文件 TIPS 说明\n */\n\n// 外边距\n.wuji-margin(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 上边距\n.wuji-margin-top(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin-top: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-mt(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-margin-top(@code, @dpr, @unit);\n}\n\n// 右边距\n.wuji-margin-right(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin-right: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-mr(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-margin-right(@code, @dpr, @unit);\n}\n\n// 下边距\n.wuji-margin-bottom(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin-bottom: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-mb(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-margin-bottom(@code, @dpr, @unit);\n}\n\n// 左边距\n.wuji-margin-left(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin-left: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-ml(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-margin-left(@code, @dpr, @unit);\n}\n\n// 左右边距\n.wuji-mx(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin-left: addUnit(getSpacing(@code), @dpr, @unit);\n    margin-right: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 上下边距\n.wuji-my(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    margin-top: addUnit(getSpacing(@code), @dpr, @unit);\n    margin-bottom: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 内填充\n.wuji-padding(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 上填充\n.wuji-padding-top(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding-top: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-pt(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-padding-top(@code, @dpr, @unit);\n}\n\n// 右填充\n.wuji-padding-right(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding-right: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-pr(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-padding-right(@code, @dpr, @unit);\n}\n\n// 下填充\n.wuji-padding-bottom(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding-bottom: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-pb(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-padding-bottom(@code, @dpr, @unit);\n}\n\n// 左填充\n.wuji-padding-left(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding-left: addUnit(getSpacing(@code), @dpr, @unit);\n}\n.wuji-pl(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-padding-left(@code, @dpr, @unit);\n}\n\n// 左右填充\n.wuji-px(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding-left: addUnit(getSpacing(@code), @dpr, @unit);\n    padding-right: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 上下填充\n.wuji-py(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    padding-top: addUnit(getSpacing(@code), @dpr, @unit);\n    padding-bottom: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 绝对/相对定位-上偏移\n.wuji-top(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    top: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 绝对/相对定位-右偏移\n.wuji-right(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    right: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 绝对/相对定位-下偏移\n.wuji-bottom(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    bottom: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 绝对/相对定位-左偏移\n.wuji-left(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    left: addUnit(getSpacing(@code), @dpr, @unit);\n}\n\n// 水平和垂直方向平移\n.wuji-translate(@code-x: undefined, @code-y: undefined, @dpr: @dpr, @unit: @unit) {\n    transform: translate(addUnit(getSpacing(@code-x), @dpr, @unit), addUnit(getSpacing(@code-y), @dpr, @unit));\n}\n\n// 水平方向平移\n.wuji-translate-x(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    transform: translateX(addUnit(getSpacing(@code), @dpr, @unit));\n}\n.wuji-trans-x(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-translate-x(@code, @dpr, @unit);\n}\n\n// 垂直方向平移\n.wuji-translate-y(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    transform: translateY(addUnit(getSpacing(@code), @dpr, @unit));\n}\n.wuji-trans-y(@code: undefined, @dpr: @dpr, @unit: @unit) {\n    .wuji-translate-y(@code, @dpr, @unit);\n}\n","/**\n * @file color mixins\n * @author wuji.baidu-int.com\n *\n * 区别于color、background-color、border-color等受全局dark-mode和night-mode变量影响。\n * fill属性可以定义局部变量night-mode，这在关闭夜间模式情况下，可以单独保留svg填充颜色fill属性。\n * 究其原因：百度APP安卓夜间模式下，基本的颜色属性不能通过css定义生效，svg的颜色属性例外。建议自行测试验证和理解。\n */\n\n// TODO: 使用css变量\n// .use-cssvar() {}\n\n// mixin: 生成背景颜色\n.wuji-background-color(@code: undefined) {\n    @color-default: getColor(@code);\n    background-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                background-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                background-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            background-color: @color-default;\n        }\n    }\n}\n\n// mixin: 添加区分模式的背景图片(可置于全局mixin中)\n.wuji-background-image(@src: null, @src-dark: null, @src-night: null) {\n    & when not (@src = null) {\n        background-image: url(@src);\n    }\n    & when (@multi-mode = true) {\n        & when not (@src-dark = null) and (@dark-mode = true) and not (@src = @src-dark) {\n            @{selector-dark} & {\n                background-image: url(@src-dark);\n            }\n        }\n        & when not (@src-night = null) and (@night-mode = true) and not (@src = @src-night) {\n            @{selector-night} & {\n                background-image: url(@src-night);\n            }\n        }\n    }\n}\n\n// mixin: 生成背景颜色\n.wuji-background(@code: undefined) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成描边颜色\n.wuji-border-color(@code: undefined, @position: around) {\n    @color-default: getColor(@code);\n    @prop: if(@position = around, e('border-color'), e('border-@{position}-color'));\n    @{prop}: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                @{prop}: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                @{prop}: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            @{prop}: @color-default;\n        }\n    }\n}\n\n// mixin: 生成描边颜色\n// 当第一个参数是多值情况且固定顺序 <line-width> <line-style> <color> 时\n// 会调用line/mixin.less中的wuji-border方法，参数示例：1px solid red\n.wuji-border(@code: undefined, @position: around) when (length(@code) = 1) {\n    .wuji-border-color(@code, @position);\n}\n\n// mixn: 生成上描边颜色\n.wuji-border-top-color(@code: undefined) {\n    .wuji-border-color(@code, top);\n}\n\n// mixn: 生成下描边颜色\n.wuji-border-bottom-color(@code: undefined) {\n    .wuji-border-color(@code, bottom);\n}\n\n// mixn: 生成左描边颜色\n.wuji-border-left-color(@code: undefined) {\n    .wuji-border-color(@code, left);\n}\n\n// mixn: 生成右描边颜色\n.wuji-border-right-color(@code: undefined) {\n    .wuji-border-color(@code, right);\n}\n\n// mixin: 生成文字颜色\n.wuji-color(@code: undefined) {\n    @color-default: getColor(@code);\n    color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成svg填充颜色\n.wuji-fill(@code: undefined, @night-mode: true) {\n    @color-default: getColor(@code);\n    fill: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                fill: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                fill: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            fill: @color-default;\n        }\n    }\n}\n\n// mixin: 生成光标颜色\n.wuji-caret(@code: undefined) {\n    @color-default: getColor(@code);\n    caret-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                caret-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                caret-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            caret-color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成透明度\n.wuji-opacity(@code: undefined) {\n    opacity: getOpacity(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.wuji-root(\n    @color: CAM_X791, // GC1\n    @background: CAM_X392, // GC9\n    @selector-root: @selector-root,\n    @selector-dark: @selector-dark,\n    @selector-night: @selector-night\n) {\n    // 如果选择器参数是字符串类型，需要escape\n    @rule1: if(isstring(@selector-root), e(@selector-root), @selector-root);\n    @rule2: if(isstring(@selector-dark), e(@selector-dark), @selector-dark);\n    @rule3: if(isstring(@selector-night), e(@selector-night), @selector-night);\n\n    @{rule1} {\n        color: getColor(@color);\n        background-color: getColor(@background);\n\n        & when (@multi-mode = true) and (@dark-mode = true) {\n            &@{rule2} {\n                color: getColor(@color, Dark);\n                background-color: getColor(@background, Dark);\n            }\n        }\n\n        & when (@multi-mode = true) and (@night-mode = true) {\n            &@{rule3} {\n                color: getColor(@color, Night);\n                background-color: getColor(@background, Night);\n            }\n        }\n    }\n}\n","/**\n * @file line mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n// 设置线条背景颜色\n.set-background-color(@color) {\n    & when (iscolor(@color) = true) {\n        background-color: @color;\n    }\n    & when (iscolor(@color) = false) {\n        .wuji-background(@color);\n    }\n}\n\n// hack: 安卓夜间模式1px看不清，通过在伪元素:after上定义rgba颜色后再微调透明度\n// .hairline-nightmode() {\n//     @{selector-night} &:after {\n//         border-color: rgba(0, 0, 0, .9);\n//         background-color: rgba(0, 0, 0, .01);\n//         opacity: .3;\n//     }\n// }\n\n// 单独设置边框宽度属性\n.wuji-border-width(@code, @position: around) {\n    // 确定边框属性\n    @prop: if(@position = around, e('border-width'), e('border-@{position}-width'));\n    @{prop}: addUnit(getLine(@code), @dpr, @unit);\n}\n// 单独设置上边框宽度↑\n.wuji-border-top-width(@code) {\n    .wuji-border-width(@code, top);\n}\n// 单独设置右边框宽度→\n.wuji-border-right-width(@code) {\n    .wuji-border-width(@code, right);\n}\n// 单独设置下边框宽度↓\n.wuji-border-bottom-width(@code) {\n    .wuji-border-width(@code, bottom);\n}\n// 单独设置左边框宽度←\n.wuji-border-left-width(@code) {\n    .wuji-border-width(@code, left);\n}\n\n// 设置边框样式，参数说明：\n// @params: 第一项可以是数值或线条编码，第三项可以是颜色值或CAM编码\n// @position: around/top/right/bottom/left\n.wuji-border(@params, @position: around) when (length(@params) = 3) {\n    @border-prop: if(@position = around, e('border'), e('border-@{position}'));\n    // 宽度不是数字时按编码取值\n    @tmp-width: extract(@params, 1);\n    // @width: if(isnumber(@tmp-width), @tmp-width, addUnit(getLine(@tmp-width), @dpr, @unit));\n    @style: extract(@params, 2);\n    @color: extract(@params, 3);\n\n    // eg: 1px solid red\n    & when (iscolor(@color)) and (isnumber(@tmp-width)) {\n        @{border-prop}: @tmp-width @style @color;\n    }\n\n    // eg: L_X03 dashed red\n    & when (iscolor(@color)) and not (isnumber(@tmp-width)) {\n        @width: addUnit(getLine(@tmp-width), @dpr, @unit);\n        @{border-prop}: @width @style @color;\n    }\n\n    // eg: 1px solid CAM_X001\n    & when not (iscolor(@color)) and (isnumber(@tmp-width)) {\n        @{border-prop}: @tmp-width @style;\n        .wuji-border-color(@color, @position);\n    }\n\n    // eg: L_X03 solid CAM_X001\n    & when not (iscolor(@color)) and not (isnumber(@tmp-width)) {\n        @width: addUnit(getLine(@tmp-width), @dpr, @unit);\n        @{border-prop}: @width @style;\n        .wuji-border-color(@color, @position);\n    }\n}\n// 单独设置上边框样式↑\n.wuji-border-top(@params) {\n    .wuji-border(@params, top);\n}\n// 单独设置右边框样式→\n.wuji-border-right(@params) {\n    .wuji-border(@params, right);\n}\n// 单独设置下边框样式↓\n.wuji-border-bottom(@params) {\n    .wuji-border(@params, bottom);\n}\n// 单独设置左边框样式←\n.wuji-border-left(@params) {\n    .wuji-border(@params, left);\n}\n\n// 以容器元素border属性实现线条时的样式\n// @code: L_X*\n// @position: around/top/right/bottom/left\n// @color: color/CAM_X*\n// @radius: J_X*\n.wuji-line(@code: L_X03, @position: around, @color: #000, @radius: false) {\n    // 判断是否需要添加伪元素\n    @pseudo: if(getLine(@code) < 3, after, false);\n\n    // 线条宽度不小于3时添加边框样式\n    & when (@pseudo = false) {\n        .wuji-border(@code solid @color, @position);\n    }\n\n    // 线条宽度小于3时添加伪元素\n    & when (@pseudo = after) {\n        position: relative;\n        overflow: visible;\n\n        &:after {\n            content: '';\n            position: absolute;\n            left: 0;\n            top: 0;\n            pointer-events: none;\n            transform-origin: 0 0;\n            .wuji-border(if(@code = L_X01, 1px, 2px) solid @color, @position);\n        }\n\n        .set-media(1.5, {\n            &:after {\n                width: 150%;\n                height: 150%;\n                // less4以下\n                // transform: scale(1 / 1.5);\n                // less4以上，除运算要额外使用括号括起来\n                // transform: scale((1 / 1.5));\n                transform: scale(0.66666667);\n            }\n        });\n\n        .set-media(2, {\n            &:after {\n                width: 200%;\n                height: 200%;\n                transform: scale(0.5);\n            }\n        });\n\n        .set-media(3, {\n            &:after {\n                width: 300%;\n                height: 300%;\n                transform: scale(0.33333333);\n            }\n        });\n    }\n\n    // 按圆角编码添加圆角\n    & when not (@radius = false) {\n        .wuji-border-radius(@radius, around, @pseudo);\n    }\n}\n\n// 以元素宽高实现线条时的样式\n// @code: L_X*\n// @color: color/CAM_X*\n// @direction: horizontal/vertical\n// @width: horizontal时有效\n// @height: vertical时有效\n.wuji-divider(@code: L_X03, @color: transparent, @direction: horizontal, @width: auto, @height: auto) {\n    // 判断是否需要添加伪元素\n    @pseudo: if(getLine(@code) < 3, after, false);\n    // 线宽属性\n    @line-prop: if(@direction = horizontal, height, width);\n    // 线长属性\n    @size-prop: if(@direction = horizontal, width, height);\n    // 设置线长\n    @{size-prop}: if(@size-prop = width, @width, @height);\n\n    // 线条宽度不小于3时添加边框样式\n    & when (@pseudo = false) {\n        // 设置线宽\n        @{line-prop}: addUnit(getLine(@code), @dpr, @unit);\n        // 添加背景色\n        .set-background-color(@color);\n    }\n\n    // 线条宽度小于3时添加伪元素，本身占宽度1px\n    & when (@pseudo = after) {\n        position: relative;\n        background-color: transparent;\n        @{line-prop}: 1px;\n\n        // 水平方向按Y轴缩放，垂直方向按X轴缩放\n        @scale: if(@direction = horizontal, scaleY, scaleX);\n\n        &:after {\n            content: '';\n            position: absolute;\n            left: 0;\n            top: 0;\n            transform-origin: 0 0;\n            @{line-prop}: if(@code = L_X01, 1px, 2px);\n            @{size-prop}: 100%;\n            .set-background-color(@color);\n        }\n\n        .set-media(1.5, {\n            &:after {\n                transform: e(%('@{scale}(%d)', 0.66666667));\n            }\n        });\n\n        .set-media(2, {\n            &:after {\n                transform: e(%('@{scale}(%d)', 0.5));\n            }\n        });\n\n        .set-media(3, {\n            &:after {\n                transform: e(%('@{scale}(%d)', 0.33333333));\n            }\n        });\n    }\n}\n","/**\n * @file common mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n// 根据屏幕密度添加样式 1.5/2/3\n.set-media(@dpr, @ruleset) {\n    @media-1: ~'(-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx)';\n    @media-2: ~'(-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx)';\n    @media-3: ~'(-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx)';\n\n    & when (@dpr = 1.5) {\n        @media @media-1 {\n            @ruleset();\n        }\n    }\n\n    & when (@dpr = 2) {\n        @media @media-2 {\n            @ruleset();\n        }\n    }\n\n    & when (@dpr = 3) {\n        @media @media-3 {\n            @ruleset();\n        }\n    }\n}\n"]}]);
