/* eslint-disable fecs-camelcase */
import ubc from '@baidu/ubc-report-sdk';
import axios from 'axios';
import {env} from '@baidu/auc-base/env';
import styles from './app.module.less';
import SpyClient from 'spy-client/dist/spy-client';
import throttle from 'lodash.throttle';
import ListItem from '../components/listItem';
import axiosJsonp from 'axios-jsonp';
import AucTitlebar from '@baidu/auc-titlebar';
import {getPageMode, registerModeChange, setPageMode} from "@/lib/utils/pageMode";
import {startFontSize} from "@/lib/utils/fontSize";
import AucComponent from "@baidu/auc-base/AucComponent";
import {WujiIcon} from '@baidu/wuji-san';
import boxx from "@baidu/auc-base/boxx";
import {setShareInfo} from "@/lib/utils/setShareInfo";
import {getQueryString} from "@/lib/utils/getQueryString";
import {invokeP} from "@baidu/xbox/na/invoke";

import '@baidu/wuji-icons/svg/d20-titlebar-more-o-66.svg';

const pxToRem = value => value / 100 + 'rem';

export default class App extends AucComponent {

    static components = {
        'list-item': ListItem,
        'wuji-icon': WujiIcon,
        'auc-titlebar': AucTitlebar,
    };
    /* eslint-disable max-len */
    static template = `
        <div id="appContainer">
            <div
                class="{{styles.headContainer}}"
                s-ref="headContainer"
                style="background-image: url({{headBackground}});"
            >
                <div
                    class="{{styles.menuIcon}}"
                    s-if="isShowMenu"
                    on-click="clickMenu"
                    style="top: {{statusBarHeightRem}};"
                >
                    <wuji-icon type="d20-titlebar-more-o-66" size="66"></wuji-icon>
                </div>
            </div>
            <div
                s-if="{{showTitleBar && isImmersive}}"
                style="height: {{statusBarHeightRem}};"
                class="{{styles.titleImmersive}}">
            </div>
            <auc-titlebar
                title="百度热议榜"
                style="top: {{statusBarHeightRem}};"
                s-if="showTitleBar"
                on-click-set="clickMenu"
                s-ref="titleBar"
            ></auc-titlebar>
            <div class="{{styles.contentTop}}">
            </div><div class="{{styles.listContainer}}">
                <list-item
                    s-for="item,index in main.itemList"
                    index="{{index + 1}}"
                    data="{{item}}"
                    class="{{styles.listItem}}"
                >
                </list-item>
            </div>
        </div>
    `;
    /* eslint-enable max-len */

    static filter = {}

    static computed = {
        headBackground() {
            let mode = this.data.get('main.mode');
            let headImage = this.data.get('main.headImage') || {};
            let src = '';
            if (mode === 'darkmode') {
                src = headImage.dark;
            } else {
                src = headImage.day;
            }
            return src;
        },
        showTitleBar() {
            if ((env.isBoxSeries || env.isXiaoMiBaiPai || env.isMatrix) && env.isLightFrame) {
                return this.data.get('appShowTitleBar');
            } else {
                return false;
            }
        },
        isShowMenu() {
            return env.isBoxSeries;
        },
        statusBarHeightRem() {
            // 手动加高点，iOS可能会出现间隙
            return this.data.get('isImmersive')
                ? pxToRem(+this.data.get('statusBarHeight') + 7)
                : 0;
        },
    }

    clickMenu() {
        boxx.call('easyBrowser.showMenu');
    }

    attached() {
        this.pageInit();
    }

    pageInit() {

        let isTomas = /tomas/i.test(env.ua);

        // 获取数据
        this.fetchAllData().then(res => {
            // 检查是否显示titlebar(可能页面已经滑动了一部分)
            this.checkShowTitleBar();

            // 滚动相关事件监听
            this.scrollListen();

            this.data.set('loadEnd', true);
        });

        // 是否是沉浸式模式
        this.data.set(
            'isImmersive',
            getQueryString('isimmersive') === 'immersive'
        );

        // 状态栏高度
        this.titleBarOffsetSet().then(value => {
            this.data.set('statusBarHeight', +value);
        });

        // 暗黑模式/夜间模式监听
        this.pageModeInit();

        // 字体大小变化监听
        (env.isBoxSeries || env.isXiaoMiBaiPai || env.isMatrix) && !isTomas && env.isIOS && startFontSize();

        // 设置分享数据
        this.setShareData();

        // 设置收藏数据
        this.setFavoriteInfo();
    }

    setShareData() {
        setShareInfo({
            title: '百度热议榜',
            description: '最热最新话题，尽在百度app~',
            link: location.href,
            icon: 'https://b.bdstatic.com/searchbox/icms/searchbox/img/baidu.png'
        });
    }

    setFavoriteInfo() {
        (env.isBoxSeries || env.isMatrix) && boxx.call('feed.updateFavorInfo', {
            title: '百度热议榜',
            ukey: 'baiduapp_rebang://rebang',
            url: location.href,
            cmd: {
                mode: '2',
                url: 'baiduboxapp://easybrowse?opentype=1&sfrom=feed&openurl=%7B%22switches%22%3A%224%22%2C%22showtitlebar%22%3A%220%22%2C%22showtoolbar%22%3A%221%22%2C%22toolbaricons%22%3A%7B%22toolids%22%3A%5B%223%22%5D%7D%2C%22menuitem%22%3A%7B%7D%7D&isla=0&rbtnstyle=0&toolbaricons=2&menumode=%!s(MISSING)'
            }
        });
    }

    async pageModeInit() {
        if (env.isBoxSeries || env.isXiaoMiBaiPai || env.isMatrix) {
            const modeMap = {
                lightmode: 'lightmode',
                nightmode: 'nightmode',
                darkmode: 'darkmode'
            };

            setPageMode();
            const pageMode = await getPageMode();
            this.data.set('pageMode', modeMap[pageMode]);
            registerModeChange((mode) => {
                this.data.set('pageMode', modeMap[mode]);
            });
        }
    }

    getData(page, pagesize = 20) {
        const HOST = 'https://ext.baidu.com';
        return axios.get(`${HOST}/api/hotrank/v1/common/get`, {
            params: {
                page,
                pagesize
            },
            adapter: axiosJsonp,
            callbackParamName: 'callback'
        }).then(res => {
            // res.data = {
            //     "errno": 0, "request_id": "1606298372", "errmsg": "", "data": {
            //         "total": 3,
            //         "conf": {
            //             "head_image": {
            //                 "day": "https://b.bdstatic.com/searchbox/image/gcp/20220915/3500642166.png",
            //                 "night": "https://b.bdstatic.com/searchbox/image/gcp/20220915/3713084190.png",
            //                 "dark": "https://b.bdstatic.com/searchbox/image/gcp/20220915/3500642166.png"
            //             }
            //         },
            //         "itemlist": [{
            //             "rank_num": 2,
            //             "id": "sv_2831215558757860542",
            //             "layout": "image1",
            //             "data": {
            //                 "mode": "video",
            //                 "title": "俄媒：赫尔松决定就加入俄罗斯举行全民公投 俄罗斯乌克兰局势 点击关注",
            //                 "source": "每日经济新闻",
            //                 "comment_num": 147,
            //                 "cmd": {"url": ""},
            //                 "image": "http://vd3.bdstatic.com/mda-nijiqagbtj2vk5ea/1663679558/mda-nijiqagbtj2vk5ea00000000.jpg",
            //                 "comment_bar": [],
            //                 "duration": "00:00:15"
            //             }
            //         }, {
            //             "rank_num": 3,
            //             "id": "sv_4798762803129369267",
            //             "layout": "image1",
            //             "data": {
            //                 "mode": "video",
            //                 "title": "小伙上初中时借住在伯父伯母家，工作后每年都会来探望，堂姐：每次送礼都跟进货似的（乐行观察）",
            //                 "source": "法制云播报",
            //                 "comment_num": 3280,
            //                 "cmd": {"url": ""},
            //                 "image": "http://pic.rmb.bdstatic.com/bjh/video/48ea24a25d3f2b258b0cbe01fb28de25.jpeg?for=bg",
            //                 "comment_bar": {"content": "懂得感恩的孩子懂得感恩的孩子懂得感恩的孩子懂得感恩的孩子懂得感恩的孩子懂得感恩的孩子懂得感恩的孩子[赞同]", "is_god_comment": 0, "scheme": ""},
            //                 "duration": "00:00:11"
            //             }
            //         }]
            //     }
            // };
            if (+res.data.errno === 0) {
                return res.data.data;
            } else {
                return Promise.reject('request errno');
            }
        });
    }

    scrollListen() {
        window.addEventListener('scroll', throttle(() => {
            // titlebar的动态显示
            this.checkShowTitleBar();
        }, 100));
    }

    checkShowTitleBar() {
        const headContainer = this.ref('headContainer');
        const rect = headContainer.getBoundingClientRect();
        this.data.set(
            'appShowTitleBar',
            // 滑动过头图区域简单按照头图2/3区域判断
            window.scrollY > headContainer.offsetTop + rect.height * 2 / 3
        );
    }

    fetchAllData() {
        let index = 1;
        const pageSize = 20;

        const loop = (resolve) => {
            return this.fetchOnePage(index, pageSize).then(data => {
                if (index * pageSize < data.total) {
                    index++;
                    loop(resolve);
                } else {
                    return resolve();
                }
            });
        }

        return new Promise((resolve) => {
            loop(resolve);
        });
    }

    fetchOnePage(page, pageSize = 20) {
        return this.getData(page, pageSize).then(data => {
            this.storeDispatch('main:updateHeadImage', data.conf.head_image);
            this.storeDispatch('main:updateItemList', data.itemlist);
            return data;
        });
    }

    // 当是沉浸式时，获取状态栏高度
    titleBarOffsetSet() {
        return new Promise((resolve, reject) => {
            if (!this.data.get('isImmersive')) {
                resolve(0);
                return;
            }
            if (env.isBoxSeries || env.isMatrix) {
                if (env.isIOS) {
                    invokeP('v16/utils/getStatusBarHeight').then(res => {
                        resolve(+res.height);
                    }, () => {
                        resolve(this.data.get('defaultStatusBarHeight'));
                    });
                } else {
                    resolve(this.data.get('defaultStatusBarHeight'));
                }
            }
        });
    }

    initData() {
        return {
            list: [],
            styles,
            appShowTitleBar: false,
            isImmersive: false,
            defaultStatusBarHeight: 35, // 当获取不到状态栏高度时的默认值
            statusBarHeight: 0, // 手机状态栏高度
            defaultTitleBarHeight: 44, // titlebar高度
            loadEnd: false,
        };
    }
}

