kground-color: rgba(255, 51, 51, 1);\n}\n.w-live-status-wrap .w-live-status-blue {\n  color: rgba(255, 255, 255, 1);\n  background-color: rgba(78, 110, 242, 1);\n}\n.w-live-status-wrap .w-live-status-gray {\n  color: rgba(255, 255, 255, 1);\n  background-color: rgba(118, 140, 174, 1);\n}\n.w-live-status-wrap .w-live-status-icon {\n  margin-right: 0.02rem;\n}\n.w-live-status-wrap .w-live-status-number {\n  margin-left: 0.03rem;\n  margin-right: 0.05rem;\n}\n.w-live-status-wrap .w-live-status-live-icon {\n  display: block;\n  width: 0.11rem;\n  height: 100%;\n  color: inherit;\n  stroke: none;\n  fill: currentColor;\n}\n.w-live-status-wrap .w-live-status-live-icon .w-live-status-line {\n  -webkit-animation: liveWave 0.68s linear infinite;\n          animation: liveWave 0.68s linear infinite;\n}\n.w-live-status-wrap .w-live-status-live-icon .w-live-status-line1 {\n  -webkit-animation-delay: -0.34s;\n          animation-delay: -0.34s;\n}\n.w-live-status-wrap .w-live-status-live-icon .w-live-status-line2 {\n  -webkit-animation-delay: 0.17s;\n          animation-delay: 0.17s;\n}\n@-webkit-keyframes liveWave {\n  0% {\n    height: 30%;\n    -webkit-transform: translateY(35%);\n            transform: translateY(35%);\n  }\n  25% {\n    height: 70%;\n    -webkit-transform: translateY(15%);\n            transform: translateY(15%);\n  }\n  50% {\n    height: 100%;\n    -webkit-transform: translateY(0);\n            transform: translateY(0);\n  }\n  75% {\n    height: 70%;\n    -webkit-transform: translateY(15%);\n            transform: translateY(15%);\n  }\n  100% {\n    height: 30%;\n    -webkit-transform: translateY(35%);\n            transform: translateY(35%);\n  }\n}\n@keyframes liveWave {\n  0% {\n    height: 30%;\n    -webkit-transform: translateY(35%);\n            transform: translateY(35%);\n  }\n  25% {\n    height: 70%;\n    -webkit-transform: translateY(15%);\n            transform: translateY(15%);\n  }\n  50% {\n    height: 100%;\n    -webkit-transform: translateY(0);\n            transform: translateY(0);\n  }\n  75% {\n    height: 70%;\n    -webkit-transform: translateY(15%);\n            transform: translateY(15%);\n  }\n  100% {\n    height: 30%;\n    -webkit-transform: translateY(35%);\n            transform: translateY(35%);\n  }\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-san/es/wuji-live-status/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/radius/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/font/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/spacing/mixins.less"],"names":[],"mappings":"AAAA;;;EAGE;ACHD;ECUG,4BAAA;EAAA,oBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,eAAA;EACA,oBAAA;ECQI,mBAAA;EC8GJ,6BAAA;EApHA,oCAAA;ECIA,kBAAA;EAZA,gBAAA;EAqCA,cAAA;AL7BJ;ACfC;EE0Be,WAAA;AHRhB;AMNQ;EAAA;IHiBY,qBAAA;EHPlB;AACF;AMLQ;EAAA;IHeY,mBAAA;EHNlB;AACF;AMJQ;EAAA;IHaY,mBAAA;EHLlB;AACF;ACjCC;ECmBO,eAAA;EACA,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;EACA,gBAAA;EKgEJ,qBAAA;EAhBA,sBAAA;EJlDI,mBAAA;AHsBR;AC3CC;EE0Be,WAAA;AHoBhB;AMlCQ;EAAA;IHiBY,qBAAA;EHqBlB;AACF;AMjCQ;EAAA;IHeY,mBAAA;EHsBlB;AACF;AMhCQ;EAAA;IHaY,mBAAA;EHuBlB;AACF;AEjCQ;EEuGJ,6BAAA;EApHA,sCAAA;AJkDJ;AEjCQ;EEmGJ,6BAAA;EApHA,uCAAA;AJsDJ;AEjCQ;EE+FJ,6BAAA;EApHA,wCAAA;AJ0DJ;ACzEC;EMsBG,qBAAA;APsDJ;AC5EC;EMsCG,oBAAA;EAhBA,qBAAA;AP0DJ;AChFC;ECoDO,cAAA;EACA,cAAA;EACA,YAAA;EACA,cAAA;EACA,YAAA;EACA,kBAAA;AF+BR;ACxFC;EC2DW,iDAAA;UAAA,yCAAA;AFgCZ;AC3FC;E