/**
 * @file 组件 WujiRank 无极-榜单
 * @author yanghongxue <yanghongxue@baidu.com>
 */

import './style/index.less';
import {Component, DataTypes} from 'san';

const prefixCls = 'w-rank';

export default class WujiRank extends Component {
    static template = /* html */ `
        <div class="${prefixCls} ${prefixCls}-{{index}}">
            {{index}}
        </div>
    `;

    static dataTypes = {
        index: DataTypes.oneOfType([
            DataTypes.string,
            DataTypes.number,
        ]),
    };

    static trimWhitespace = 'all';

    initData() {
        return {
            index: 1,
        };
    }
}
