import BaseLog from '@baidu/feed-baselog';

export enum ACT_TYPE {
    SHOW = 1,
    CLICK = 2
}

export const sendLog = (type, rid) => {
    const baseParams = {
        ct: 3,
        // 点击 cst 2 ，展现 cst 1
        cst: type,
        logFrom: 'feed_midbox',
        logInfo: 'a2_l_event_topic',
        r: Date.now()
    };
    const baseIns = new BaseLog({
        mode: 1,
        tParams: baseParams
    });
    baseIns.sendLog({
        rid
    });
}
